.menu {
  margin-left: 39px;
  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-around;
  }
  &__item {
    &:last-child {
      a {
        margin-right: 0;
      }
    }
  }

  &__link {
    display: block;
    padding-top: 1px;
    color: rgba(#000, 0.8);
    font-weight: 700;
    margin-right: 32px;

    &:hover {
      color: #014c8c;
    }
  }

  @media (max-width: 768px) {
    transform: scale(0);
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    transition: transform 0.3s;

    &.active {
      transform: scale(1);
      background: #fff;
      z-index: 10;
    }

    &__list {
      flex-direction: column;
      align-items: center;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    &__item + &__item {
      margin-top: 10px;
    }
  }
}
