.middle {
  margin: 0 auto;
  position: relative;
  width: 100%;
  @include clearfix;
  background-color: #f5f8fa;
  display: flex;
  justify-content: center;
  max-width: 1440px;
  .content {
    width: calc((100% / 12) * 9);
    background: url("../images/kitchen.svg") no-repeat;
    background-position: bottom;
    box-sizing: border-box;
    padding-left: 16px;
    padding-right: 16px;
    &__news {
      width: calc((100% / 12) * 3);
      box-sizing: border-box;
      padding-left: 16px;
    }
    &_navigation {
      display: flex;
      flex-wrap: wrap;
      .a_navigation {
        width: 100%;
        padding: 10px 15px;
      }
    }
    &_index_11 {
      width: 100%;
    }
  }
  .middle__content {
    width: unset;
  }
  &__title {
    &_h2 {
      display: flex;
      align-items: center;
      font-family: "Exo2-Regular";
      font-size: 24px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.17;
      letter-spacing: normal;
      color: #293742;
      margin-top: 24px;
      margin-bottom: 12px; // index_2.html
      &_a {
        font-family: "Exo2-Semi";
        font-size: 24px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.17;
        letter-spacing: normal;
        color: #4580e6;
        margin-left: 8px;
        margin-right: 4px;
      }
      &_img {
        width: 24px;
        height: 24px;
      }
    }
    &__select {
      display: flex;
      height: 40px;
      &_score {
        width: 100%;
        max-width: 115px;
      }
      &_category {
        width: 100%;
        max-width: 224px;
      }
      select {
        border-radius: 4px;
        box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
        background-color: #ffffff;
        border: solid 1px #d8e1e8;
        padding-left: 5px;
        font-family: "Exo2-Semi";
        font-size: 12px;
        text-transform: uppercase;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.8px;
        color: #bfccd6;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: url("../images/chevron-down.svg") 96% / 16px no-repeat white;
      }
      &_option {
        font-family: "Exo2-Semi";
        color: #394b59;
        font-size: 12px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.8px;
        color: #394b59;
      }
      /*target Internet Explorer 9 and Internet Explorer 10:*/
      @media screen and (min-width: 0\0) {
        select {
          background: none;
          padding: 5px;
        }
      }
      form {
        position: relative;
        border-radius: 4px;
        box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
        background-color: #ffffff;
        border: solid 1px #d8e1e8;
        width: 100%;
        display: flex;
        align-items: center;
        input,
        button {
          border: none;
          outline: none;
          background: transparent;
        }
        input {
          padding-left: 5px;
          font-family: "SourceSansPro";
          font-size: 14px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.14;
          letter-spacing: normal;
          color: #bfccd6;
          opacity: 0.6;
        }
        .button {
          width: 16px;
          height: 16px;
          object-fit: contain;
          cursor: pointer;
          &_settings {
            margin: 0 0 0 auto;
          }
          &_magnifyingglass {
            margin-right: 12px;
            margin-left: 12px;
          }
        }
      }
      &_index_11 {
        height: unset;
        margin-bottom: 15px;
        .item {
          &_main {
            margin-right: 32px;
            width: 240px;
            display: flex;
            flex-wrap: wrap;
            margin-top: 12px;
            select {
              height: 32px;
            }
            &-last {
              width: 160px;
              margin-right: 0;
            }
          }
          &_date {
            width: 100%;
            height: 32px;
            border-radius: 4px;
            box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
            border: solid 1px #d8e1e8;
            text-transform: uppercase;
            font-family: "Exo2-Semi";
            font-size: 12px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.33;
            letter-spacing: 0.8px;
            color: #bfccd6;
            padding-left: 12px;
            &::-webkit-calendar-picker-indicator {
              width: 13px;
              height: 13px;
              background: url("https://icongr.am/fontawesome/calendar.svg?color=bfccd6") no-repeat center;
              background-size: contain;
              color: transparent;
              margin-right: 12px;
            }
            &::-webkit-clear-button {
              display: none;
            }
            &::-webkit-inner-spin-button {
              display: none;
            }
          }
          &_name {
            font-family: 'SourceSansPro';
            font-size: 10px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.6;
            letter-spacing: 1px;
            color: #919eab;
            margin: 4px 0;
          }
          &_buttons {
            width: 208px;
            position: relative;
            margin: 0 0 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            .button {
              position: relative;
              display: flex;
              align-items: center;
              width: 100%;
              height: 40px;
              font-family: "Exo2-Semi";
              font-size: 12px;
              font-weight: 600;
              font-style: normal;
              font-stretch: normal;
              line-height: 1.25;
              letter-spacing: 0.8px;
              color: #ffffff;
              text-decoration: none;
              text-transform: uppercase;
              outline: none;
              border-radius: 4px;
              border: solid 1px #394b5985;
              transition: 0.2s ease-in-out;
              padding: 12px 12px 13px;
              opacity: .4;
              &_save {
                background-color: #4580e6;
                .img {
                  background-image: url("https://icongr.am/clarity/check.svg?color=ffffff");
                  background-size: contain;
                  width: 16px;
                  height: 16px;
                  margin: 0 0 0 auto;
                }
                &:hover {
                  opacity: 1;
                }
              }
            }
            label {
              margin-top: 10px;
              margin-bottom: 11px;
            }
          }
        }
      }
    }
    .city_select {
      width: 100%;
      max-width: 160px;
      border-radius: 4px;
      color: #394b59;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url("../images/chevron-down.svg") 96% / 16px no-repeat #f5f8fa !important;
      border-radius: 4px;
      box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
      background-color: #ffffff;
      border: solid 1px #d8e1e8;
      padding-left: 5px;
      font-family: "Exo2-Semi";
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: 0.8px;
      color: #bfccd6;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url("../images/chevron-down.svg") 96% / 16px no-repeat white;
    }
    &__pagination {
      display: flex;
      margin-top: 24px;
      margin-bottom: 16px;
      justify-content: space-between;
      &_select {
        width: 100%;
        max-width: 160px;
        border-radius: 4px;
        color: #394b59;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: url("../images/chevron-down.svg") 96% / 16px no-repeat #f5f8fa !important;
      }
      &_h4 {
        font-family: "SourceSansPro";
        font-size: 16px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: normal;
        text-align: center;
        color: #394b59;
        margin-bottom: 0;
        margin-left: 5%;
        &_span {
          font-weight: 600;
          color: #293742;
        }
      }
    }
    select {
      border-radius: 4px;
      box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
      border: solid 1px #d8e1e8;
      padding-left: 5px;
      font-family: "Exo2-Semi";
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: 0.8px;
      color: #bfccd6;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url("../images/chevron-down.svg") 96% / 16px no-repeat white;
    } // Index_4.html
    &__model_page {
      display: flex;
      margin-top: 24px;
      margin-bottom: 36px;
      .model_page_description {
        width: 100%;
        max-width: 328px;
        margin-right: 32px;
        &_text {
          &_h2 {
            font-family: "Exo2-Regular";
            font-size: 32px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.38;
            letter-spacing: normal;
            color: #293742;
            margin-bottom: 0;
          }
          &_h5 {
            width: 100%;
            font-family: "Exo2-Regular";
            font-size: 12px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1;
            letter-spacing: normal;
            color: #ced9e0;
            margin-bottom: 0;
            height: 50px;
          }
        }
        &_checkbox {
          width: 100%;
          margin-bottom: 10px;
          label {
            margin-bottom: 0;
          }
        }
        &_buttons {
          display: flex;
          align-items: center;
          .model_page_button {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 146px;
            height: 40px;
            font-family: "Exo2-Semi";
            font-size: 12px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.25;
            letter-spacing: 0.8px;
            color: #ffffff;
            text-decoration: none;
            text-transform: uppercase;
            padding: 0.5em 1em;
            outline: none;
            border-radius: 4px;
            background-color: #4580e6;
            border: solid 1px #394b5985;
            transition: 0.2s ease-in-out;
            margin-right: 16px;
            &:active {
              top: 1px;
              opacity: 0.6;
              border-radius: 4px;
              background-image: linear-gradient(to bottom, #ffffff, #637381);
              box-shadow: 0 0 1px rgba(0, 0, 0, 0.5) inset, 0 2px 3px rgba(0, 0, 0, 0.5) inset, 0 1px 1px rgba(255, 255, 255, 0.1);
            }
            .description_button {
              margin-left: 20px;
              width: 16px;
              height: 16px;
              background-size: contain;
              background-image: url("https://icongr.am/feather/plus.svg?color=ffffff");
            }
          }
        }
        .city_select {
          width: 166px;
          height: 32px;
        }
      }
      .model_page_table {
        border-radius: 4px;
        border: dashed 2px #d8e1e8;
        display: flex;
        flex-wrap: wrap;
        &_h5,
        &_h5_two {
          width: 50%;
          border-bottom: dashed 1px #d8e1e8;
          border-right: dashed 1px #d8e1e8;
          margin-bottom: 0;
          font-family: "SourceSansPro";
          font-size: 14px;
          font-weight: normal;
          color: #919eab;
          font-style: normal;
          font-stretch: normal;
          line-height: 2.86;
          letter-spacing: normal;
          display: flex;
          align-items: center;
          &:nth-last-child(-n+2) {
            border-bottom: none;
          }
          &_img {
            width: 12px;
            height: 12px;
            margin: 0 8px 0 12px;
            background-image: url("https://icongr.am/clarity/copy.svg?color=b3b5cb");
            background-size: contain;
          }
          span {
            font-weight: 600;
            color: #454f5b;
            margin-left: 5px;
          }
        }
        &_h5_two {
          width: 50%;
          border-right: none;
          margin-bottom: 0;
          padding-left: 24px;
          b &:last-child {
            border-bottom: none;
          }
          &_span {
            &_red {
              color: #f55656 !important;
            }
            &_green {
              color: #15b371 !important;
            }
            &_violet {
              color: #a854a8 !important;
            }
          }
        }
      }
    }
    &_index_10 {
      .middle__title {
        &__select {
          flex-wrap: wrap;
          height: unset;
          .item {
            height: 40px;
            max-width: 225px;
            width: 100%;
            padding-left: 16px;
            &_form {
              max-width: 926px;
              width: 100%;
              border-radius: 4px;
              box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
              background-color: #ffffff;
              border: solid 1px #d8e1e8;
              margin-right: 32px;
              margin-bottom: 8px;
              input {
                width: 100%;
                padding-left: none;
              }
            }
            &_date {
              width: 100%;
              max-width: 220px;
              border-radius: 4px;
              box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
              border: solid 1px #d8e1e8;
              text-transform: uppercase;
              font-family: "Exo2-Semi";
              font-size: 12px;
              font-weight: 600;
              font-style: normal;
              font-stretch: normal;
              line-height: 1.33;
              letter-spacing: 0.8px;
              color: #bfccd6;
              padding-left: 12px;
              &::-webkit-calendar-picker-indicator {
                width: 13px;
                height: 13px;
                background: url("https://icongr.am/fontawesome/calendar.svg?color=bfccd6") no-repeat center;
                background-size: contain;
                color: transparent;
                margin-right: 12px;
              }
              &::-webkit-clear-button {
                display: none;
              }
              &::-webkit-inner-spin-button {
                display: none;
              }
              &_last {
                max-width: 273px;
              }
            }
          }
        }
        &__pagination {
          &_select {
            border-radius: 4px;
            box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
            border: solid 1px #d8e1e8;
            text-transform: uppercase;
            font-family: "Exo2-Semi";
            font-size: 12px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.33;
            letter-spacing: 0.8px;
            padding-left: 16px;
          }
        }
      }
      .middle__brands_table {
        .span {
          &__number {
            margin-right: 19px;
          }
        }
      }
    }
    &__link-index_11 {
      display: flex;
      flex-wrap: wrap;
      margin-left: -8px;
      margin-bottom: 16px;
      .link-buttons {
        padding: 5px 8px 7px 9px;
        border-radius: 3px;
        border: solid 1px #dcdee0;
        font-family: "Exo2-Semi";
        font-size: 12px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.4px;
        text-align: center;
        color: #bfccd6;
        margin-left: 8px;
        margin-bottom: 8px;
        &:hover {
          color: #8a9ba8;
          border: solid 1px #b3b5cb;
        }
      }
    }
    &_report_3 {
      .middle__title_h2_a {
        background: url("https://icongr.am/entypo/chevron-down.svg?color=c4cdd5") 100% / 30px no-repeat;
        padding-right: 30px;
        &:hover {
          background: url("https://icongr.am/entypo/chevron-down.svg?color=4580e6") 100% / 30px no-repeat;
        }
      }
    }
  }
  &__brands {
    &_monobrands {
      display: flex;
      flex-wrap: wrap;
      padding-left: 16px;
    }
    &_multibrand {
      display: flex;
      padding-left: 16px;
      margin-bottom: 12%;
    }
    &_list {
      width: 18%;
      display: flex;
      flex-wrap: wrap;
      margin-right: 9.333333333333333%;
      height: 8%;
      &:last-child {
        margin-right: 0;
      }
    }
    &__h3 {
      font-family: "Exo2-Regular";
      font-size: 20px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.2;
      letter-spacing: normal;
      color: #293742;
      margin-top: 40px;
      margin-bottom: 16px;
      width: 100%;
    }
    &_link {
      width: 100%;
      font-family: "Exo2-Regular";
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #2b95d6;
      margin-top: 12px;
      margin-bottom: 12px;
      height: 8.333333333333333%;
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    } // INDEX_3.html
    &_table {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      &_title,
      &_block {
        width: 100%;
        border-radius: 4px;
        box-shadow: 8px 8px 16px 0 rgba(191, 204, 214, 0.2);
        background-color: #ffffff;
        border: solid 1px #d8e1e8;
        height: 48px;
        align-items: center;
        display: flex;
        .span {
          font-family: "Exo2-Semi";
          font-size: 14px;
          font-weight: 600;
          font-style: normal;
          font-stretch: normal;
          line-height: 1;
          letter-spacing: normal;
          color: #bfccd6;
          display: flex;
          align-items: center;
          &__brand {
            padding-left: 22px;
            width: 11%;
          }
          &__model {
            width: 11.45038167938931%;
            padding-left: 16px;
            &:hover {
              color: #394b59;
            }
            &_bars {
              width: 10px;
              height: 12px;
              object-fit: contain;
              background-image: url("https://icongr.am/octicons/three-bars.svg?color=bfccd6");
              background-repeat: no-repeat;
              background-size: cover;
              &:hover {
                background-image: url("https://icongr.am/octicons/three-bars.svg?color=4580e6");
              }
            }
            &_arrows {
              width: 14px;
              height: 14px;
              object-fit: contain;
              background-image: url("https://icongr.am/entypo/select-arrows.svg?color=bfccd6");
              background-repeat: no-repeat;
              background-size: cover;
              margin-left: 10px;
              &:hover {
                background-image: url("https://icongr.am/entypo/select-arrows.svg?color=4580e6");
              }
            }
          }
          &__availability {
            width: 7.6%;
            margin-left: 4.8%;
          }
          &__sale {
            width: 9%;
            font-weight: bold;
            margin-left: 7.7%;
            &:hover {
              color: #394b59;
            }
          }
          &__purchase {
            width: 8%;
            margin-left: 2.7%;
          }
          &__commission {
            width: 9%;
            margin-left: 3.3%;
          }
          &__formula {
            width: 11%;
            margin-left: 7.2%;
          }
          &__unid {
            width: 6.488549618320611%;
            padding-left: 1.2%;
          }
        }
      }
      &_block {
        border-top: none;
        .span {
          font-family: "SourceSansPro";
          font-size: 14px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.43;
          letter-spacing: normal;
          height: 20px;
          &__cost {
            margin-left: 5px;
          }
          &__plus {
            margin: 0 5px;
          }
          &__cost,
          &__plus {
            color: #bfccd6;
          }
          &__brand {
            color: #293742;
            padding-left: 24px;
          }
          &__model {
            color: #4580e6;
            font-weight: 600;
            padding-left: 32px;
          }
          &__availability {
            font-size: 10px;
            font-weight: bold;
            line-height: 1.2;
            letter-spacing: 0.8px;
            text-align: center;
            border-radius: 3px;
            background-color: #2cb76b;
            font-weight: bold;
            text-transform: uppercase;
            color: #ffffff;
            padding-left: 0;
            justify-content: center;
          }
          &__sale {
            color: #293742;
            padding-left: 0.4%;
          }
          &__purchase {
            color: #293742;
            padding-left: 0.5%;
          }
          &__commission {
            color: #293742;
            padding-left: 0.3%;
          }
          &__formula {
            color: #293742;
            font-weight: 600;
            text-transform: uppercase;
            padding-left: 0.4%;
          }
          &__unid {
            color: #293742;
            display: flex;
            justify-content: flex-end;
          }
        }
        .span__math {
          background-image: url("https://icongr.am/feather/plus.svg?color=d8e1e8");
          min-width: 20px;
          height: 20px;
          background-size: cover;
          position: absolute;
          right: 16px;
          &:hover {
            background-image: url("https://icongr.am/feather/plus.svg?color=007ace");
          }
        }
        &_checkbox {
          display: flex;
          margin-left: 77px;
          .span__availability {
            width: unset;
            margin-left: 8px;
            padding: 4px 8px;
            min-width: 75px;
          }
          &_color {
            .slideThree,
            .span__availability {
              background-color: #f2b824 !important;
            }
          }
          .span__sale {
            width: unset;
            margin-left: 8px;
          }
          .slideThree {
            width: 56px;
            height: 20px;
            background: #15b371;
            position: relative;
            border-radius: 50px;
            font-family: "Exo2-Bold";
            font-size: 10px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.6;
            letter-spacing: 0.8px;
            text-transform: uppercase;
            &:after {
